body {
  font-family: Comfortaa, sans-serif;
}

.breed-select {
  margin-bottom: 50px;
}

.cat-info {
  display: flex;
}

.cat-card_info {
  flex-direction: column;
  justify-content: flex-start;
  margin-left: 20px;
  display: flex;
}

.is-hidden {
  opacity: 0;
}

.loader {
  width: 64px;
  height: 64px;
  background: #fff;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%);
}

.loader:before {
  content: "";
  width: 40px;
  height: 40px;
  background: #ff9371;
  animation: slide 2s ease-in-out infinite alternate;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(45deg)translate(30%, 40%);
  box-shadow: 32px -34px 0 5px #66b2ff;
}

.loader:after {
  content: "";
  width: 16px;
  height: 16px;
  transform-origin: 35px 145px;
  background: #66b2ff;
  border-radius: 50%;
  animation: rotate 2s ease-in-out infinite;
  position: absolute;
  top: 10px;
  left: 10px;
  transform: rotate(0deg);
}

@keyframes slide {
  0%, 100% {
    bottom: -35px;
  }

  25%, 75% {
    bottom: -2px;
  }

  20%, 80% {
    bottom: 2px;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(-15deg);
  }

  25%, 75% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(25deg);
  }
}

